        * {
            color: aliceblue;
            margin: 0;
            padding: 0;
            text-decoration: none;
            font-family: 'Raleway';
            /* border: 1px solid red; */
        }
        .error{
            @keyframes text-focus-in {
              0% {
                filter: blur(12px);
                opacity: 0;
              }
              100% {
                filter: blur(0px);
                opacity: 1;
              }
            }
            .trista{
                color: black;
            }
            .errorcont {
              color:black;
              width: 100vw;
              height: 93vh;
              background-color: #F7BA00;
              display: flex;
              gap: 0;
              flex-direction: column;
              justify-content: center;
              align-items: center;
            }
            .errorcont img {
              width: 20%;
              height: auto;
            }
            
            .logocont {
              background-color: #F7BA00;
              width: 100%;
              height: 7vh;
              display: flex;
              justify-content: center;
              align-items: flex-start;
            }
            
            .logo {
              margin-left: 2vw;
              margin-top: 2vw;
              text-align: center;
              width: 250px;
              background-color: aliceblue;
              border: 1px solid aliceblue;
              border-radius: 20px;
              animation: text-focus-in 0.8s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
            }
            .logo h1 {
              color: black;
            }
            
            @media (min-width: 357px) and (max-width: 768px) {
              .logocont {
                justify-content: center;
                align-items: center;
              }
              .errorcont img {
                width: 60%;
                height: auto;
              }
            }
          }
        
        @keyframes text-focus-in {
            0% {
                -webkit-filter: blur(12px);
                filter: blur(12px);
                opacity: 0;
            }
            100% {
                -webkit-filter: blur(0px);
                filter: blur(0px);
                opacity: 1;
            }
        }
        
        @keyframes tracking-in-expand {
            0% {
                letter-spacing: -0.5em;
                opacity: 0;
            }
            40% {
                opacity: 0.6;
            }
            100% {
                opacity: 1;
            }
        }
        
        .logocont {
            width: 95vw;
            height: 7vh;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        .logo {
            h1 {
                color: black;
            }
            text-align: center;
            width: 250px;
            background-color: aliceblue;
            border: 1px solid aliceblue;
            border-radius: 20px;
            animation: text-focus-in 0.8s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
        }
        
        .container {
            min-height: 100vh;
            background-color: rgb(0, 0, 0);
        }
        
        .center {
            margin-left: calc(50% - 47.5vw);
            margin-right: calc(50% - 47.5vw);
        }
        
        .header {
            margin-top: 30px;
            width: 95vw;
            height: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 200px;
                height: 150px;
            }
        }
        
        .header_phone1 {
            margin-top: 30px;
            width: 95vw;
            height: auto;
            display: none;
            justify-content: center;
            align-items: center;
            img {
                width: 200px;
                height: 150px;
            }
        }
        
        .header_phone2 {
            margin-top: 30px;
            width: 95vw;
            height: auto;
            display: none;
            justify-content: center;
            align-items: center;
            img {
                width: 200px;
                height: 150px;
            }
        }
        .skills{
            border-bottom: 1px solid #ffc41b ;
            transition: all .4s;

        }
        .skills:hover{
            color: #ffc41b;
        }
        .headerlink {
            text-align: center;
            width: 100px;
            font-size: 21px;
            font-weight: 600;
            margin-left: 90px;
            background-color: black;
            border: 1px solid black;
            transition: all 0.4s;
        }
        
        .headerlink:first-child {
            border-left: 1px solid #ffc41b;
            ;
        }
        
        .headerlink:last-child {
            padding-right: 5px;
            border-right: 1px solid #ffc41b;
        }
        
        .headerlink:hover {
            color: black;
            background-color: aliceblue;
            /* background-color: rgb(168, 1, 104); */
            border-radius: 10px;
            border: 1px solid aliceblue;
            /* border: 1px solid rgb(168, 1, 104); */
        }
        
        .headerlink:active {
            color: black;
            background-color: aliceblue;
            /* background-color: rgb(179, 219, 0); */
            /* background-color: rgb(168, 1, 104); */
            border-radius: 10px;
            border: 1px solid aliceblue;
            /* border: 1px solid rgb(168, 1, 104); */
        }
        
        .headerlink1 {
            margin-left: 0px;
        }
        
        .welcome {
            font-weight: 400;
            height: 50px;
            color: #ffc41b;
        }
        
        .default_mastersell {
            width: 100%;
            padding-bottom: 20px;
            text-align: center;
        }
        
        .mastersell {
            width: 100%;
            padding-bottom: 20px;
            text-align: center;
            animation: tracking-in-expand 3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
        }
        
        .mastersell1 {
            display: none;
            h1 {
                font-size: 1.5em;
            }
            text-align: center;
            animation: tracking-in-expand 3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
        }
        
        .mastersell2 {
            display: none;
            h1 {
                font-size: 1.5em;
            }
            padding-bottom: 20px;
            text-align: center;
            animation: tracking-in-expand 3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
        }
        
        .body {
            width: 95vw;
            /* height: 35vw; */
            display: grid;
            grid-template-columns: 47.5vw 47.5vw;
            justify-content: center;
        }
        
        .theme {
            width: 100%;
            height: auto;
            /* border: 1px solid aliceblue; */
            /* box-shadow: 5px 5px 10px 5px rgb(54, 66, 0); */
            border-radius: 20px;
            transition: border 1s;
        }
        
        .theme:hover {
            border: 2px solid #ffc41b;
        }
        
        .item {
            padding-top: 50px;
        }
        
        .item2 {
            text-align: center;
            margin-top: 50px;
            img {
                border: 2px solid black;
                width: 80%;
                height: auto;
            }
        }
        
        .item1 {
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: wrap;
            display: flex;
            border-radius: 20px;
            margin-top: 50px;
            text-align: center;
            img {
                width: 20%;
                height: auto;
            }
            .ants {
                margin-left: 2vw;
            }
        }
        
        .ul {
            width: 75%;
            li {
                color: aliceblue;
                width: 100%;
                text-align: left;
                list-style-type: none;
                padding-top: 35px;
                /* Прячем исходные маркеры */
            }
            li::before {
                content: "▪";
                padding-right: 10px;
                color: #ffc41b;
                width: 100%;
                text-align: left;
            }
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }
        
        .li1 {
            padding-top: 10px !important;
        }
        
        .contactsblock {
            width: 75%;
            margin-top: 30px;
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
        }
        
        .telegram {
            width: 200px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all 1s;
        }
        
        .telegram:hover {
            color: #ffc41b;
            font-weight: 500;
        }
        
        .tg {
            margin-right: 5px;
        }
        
        .postms {
            width: 80%;
            color: aliceblue;
            font-weight: 400;
        }
        
        .prepostms {
            width: 100%;
            display: flex;
            justify-content: flex-end;
        }
        
        .postms2 {
            font-weight: 600;
            padding-top: 25px;
            padding-bottom: 15px;
            width: 80%;
            color: aliceblue;
        }
        
        .postms3 {
            font-weight: 600;
            padding-top: 25px;
            padding-bottom: 15px;
            width: 50%;
            color: aliceblue;
        }
        
        .footer {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        }
        
        .logo2 {
            padding-top: 60px;
            img {
                height: 60px;
                width: auto;
            }
        }
        
        .goodres {
            text-align: center;
            width: 100%;
            color: darkgray;
            font-weight: 200;
            margin-top: 30px;
        }
        
        .copyright {
            text-align: center;
            width: 100%;
            color: darkgray;
            font-weight: 200;
            margin-top: 10px;
            padding-bottom: 3vh;
        }
        
        @media(min-width:768px) and (max-width:1024px) {
            .mastersell {
                display: none;
            }
            .mastersell1 {
                display: inline;
            }
            .mastersell2 {
                display: inline;
            }
        }
        
        @media(max-width: 768px) {
            /* .body {
height: 60vh
} */
            .header {
                display: none;
            }
            .header_phone1 {
                display: flex;
            }
            .header_phone2 {
                display: flex;
            }
            .headerlink {
                font-size: 18px;
                margin-left: 45px;
            }
            .headerlink1 {
                margin-left: 0px;
            }
            .body {
                display: block;
            }
            .mastersell {
                display: none;
            }
            .mastersell1 {
                display: inline;
            }
            .mastersell2 {
                display: inline;
            }
            .item2 {
                display: none;
            }
        }